<!-- 基础漏斗图

 -->

<template>
  <div class="demo">
    <div class="box">
      <h3>基础雷达图</h3>
      <div id="container"></div>
    </div>
    <div class="box">
      <h3>雷达图(带网格)</h3>
      <div id="containerG2"></div>
    </div>
  </div>
</template>

<script>
import { Radar } from '@antv/g2plot'

export default {
  components: {},
  data() {
    return {
      data: [
        { item: 'Design', user: 'a', score: 70 },
        { item: 'Design', user: 'b', score: 30 },
        { item: 'Development', user: 'a', score: 60 },
        { item: 'Development', user: 'b', score: 70 },
        { item: 'Marketing', user: 'a', score: 50 },
        { item: 'Marketing', user: 'b', score: 60 },
        { item: 'Users', user: 'a', score: 40 },
        { item: 'Users', user: 'b', score: 50 },
        { item: 'Test', user: 'a', score: 60 },
        { item: 'Test', user: 'b', score: 70 },
        { item: 'Language', user: 'a', score: 70 },
        { item: 'Language', user: 'b', score: 50 },
        { item: 'Technology', user: 'a', score: 50 },
        { item: 'Technology', user: 'b', score: 40 },
        { item: 'Support', user: 'a', score: 30 },
        { item: 'Support', user: 'b', score: 40 },
        { item: 'Sales', user: 'a', score: 60 },
        { item: 'Sales', user: 'b', score: 40 },
        { item: 'UX', user: 'a', score: 50 },
        { item: 'UX', user: 'b', score: 60 }
      ]
    }
  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const data = [
      { name: 'G2', star: 10371 },
      { name: 'G6', star: 7380 },
      { name: 'F2', star: 7414 },
      { name: 'L7', star: 2140 },
      { name: 'X6', star: 660 },
      { name: 'AVA', star: 885 },
      { name: 'G2Plot', star: 1626 }
    ]
    const radarPlot = new Radar('container', {
      data: data.map(d => ({ ...d, star: Math.sqrt(d.star) })),
      xField: 'name',
      yField: 'star',
      appendPadding: [0, 10, 0, 10],
      meta: {
        star: {
          alias: 'star 数量',
          min: 0,
          nice: true,
          formatter: v => Number(v).toFixed(2)
        }
      },
      xAxis: {
        tickLine: null
      },
      yAxis: {
        label: false,
        grid: {
          alternateColor: 'rgba(0, 0, 0, 0.04)'
        }
      },
      // 开启辅助点
      point: {
        size: 2
      },
      area: {}
    })
    radarPlot.render()

    this.getone()
  },
  methods: {
    getone() {
      const data = this.data
      const radarPlot = new Radar('containerG2', {
        data,
        xField: 'item',
        yField: 'score',
        seriesField: 'user',
        meta: {
          score: {
            alias: '分数',
            min: 0,
            max: 80
          }
        },
        xAxis: {
          line: null,
          tickLine: null,
          grid: {
            line: {
              style: {
                lineDash: null
              }
            }
          }
        },
        yAxis: {
          line: null,
          tickLine: null,
          grid: {
            line: {
              type: 'line',
              style: {
                lineDash: null
              }
            }
          }
        },
        // 开启面积
        area: {},
        // 开启辅助点
        point: {
          size: 2
        }
      })
      radarPlot.render()
    }
  },
  created() {
    // var wrapperElement = '<div style=position:relative;></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo {
  display: flex;
  flex-flow: wrap;

  .box {
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display: inline-block;
    padding: 0 20px 20px;
    margin: 20px;

    h3 {
      line-height: 50px;
      height: 50px;
      text-align: center;
    }
  }

  #container {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerG2 {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGz {
    display: block;
    width: 500px;
    height: 300px;
  }
}
</style>



